
import Title from './Title';
import About from './About';
// import data from '../data';
import { useSelector } from "react-redux";

function Infomation(){

	let data=useSelector(state => state.title);

	// let {title}=data;
    
    return(
        <section id="page1">
            <div className="inner">
                    <Title propsValue={data.about}/>
                    <About />
            </div>
        </section>
    );
}

export default Infomation;