// import data from "../data";
import { useSelector } from "react-redux";

function Footer(){  

	// console.log(data);
	// let {footer}=data;
	let data=useSelector(state => state.footer);

    return(
        <footer>
            <p>
                <span className="top">{data.top}</span>
					{data.mid}
			</p>
                <span className="bottom">{data.bottom}</span>
        </footer>
    );
}

export default Footer;