export default configureStore({
	reducer: {
		navigation: navigation.reducer,
		title: title.reducer,
		about: about.reducer,
		subtext: subtext.reducer,
		skils: skils.reducer,
		startTitle: startTitle.reducer,
		footer: footer.reducer,
		utils: utils.reducer,
		controller: controller.reducer
	}
});