

function Title(props){

	// console.log(props.propsValue)
	let {p, h3, strong}=props.propsValue;
		// console.log(p)
	return(
		<div id="title" data-aos="flip-down" data-aos-duration="100">
			    <p>{p}</p>
                <h3>{h3}</h3>
				<strong>{strong}</strong>
		</div>
	);
}

export default Title;