
import Title from './Title';
import ContactText from './ContactText';
import { useSelector } from "react-redux";
// import data from '../data';

function Contact(){


	let utils=useSelector(state => state.utils);
	let title=useSelector(state => state.title);
	// let {utils,}=data;
	let {contact}=title;

	// console.log(title, utils);
    return(
        <section id="contact">
            <div className="inner">
                    <Title propsValue={contact}/>
                <div className="ss">
					<ContactText propsValue={utils}/>		
				<Kakao />
                </div>
            </div>
        </section>
    );
}

function Kakao(){
    return(
            <div className="kakao" data-aos="fade-down" data-aos-duration="800">
                <a href="https://open.kakao.com/o/sU0bYo9f">
                    <p>카카오톡 오픈채팅 바로가기</p>
                </a>
            </div>
    );
}

export default Contact;