import StartTitle from "./StartTitle";
import SubText from "./SubText";
// import data from "../data";
import { useSelector } from "react-redux";

function Start(){

	let data=useSelector(state => state.startTitle);

	// let {startTitle}=data;
    return(
        <section id="start"> {/* Start component */}
			<div className="text_area">
					<StartTitle propsValue={data}/>
					<SubText />
			</div>
				<video id="main_video"></video>
				<div className="dim"></div>
	</section>
    );
}

export default Start;