// import data from "../data";

function SubTextList(props){
		
	// console.log(props.propsValue)
	let {strong, span, p}=props.propsValue;
		// console.log(id);


	return(
		<li>
			<a href=""> 
				<strong>{strong}</strong>
				<span>{span}</span>
				<p>{p}</p>
			</a>
		</li>
	);
}

export default SubTextList;