// import data from "../data";
import { useSelector } from "react-redux";


function Utils(){

	// console.log(data.utils);
	let data=useSelector(state => state.utils);
	let {name, href}=data;

	console.log(name);
	console.log(href);
	return(
        <div className="utils">
            <ul>
				{
                    name.map((d, i) => <li key={i}><a href={href[i]}>{d}</a></li>)
                }

                {/* 
				<li><a href="">Codepen</a></li>
                <li><a href="">Figma</a></li>
                <li><a href="">GitHub</a></li>
				 */}
            </ul>
        </div>
	);
}

export default Utils;