// import data from "../data";
import SubTextList from "./SubTextList";
import { useSelector } from "react-redux";

function SubText(){
// let {subtext}=data;
let data=useSelector(state => state.subtext);
// console.log(data);
	return(
        <div className="sub_text">
            <ul>
				{
					data.map((d, i) => <SubTextList key={i} propsValue={d}/>)
				}
                {/* <SubTextList propsValue={subtext[0]}/>
                <SubTextList propsValue={subtext[1]}/>
                <SubTextList propsValue={subtext[2]}/>
                <SubTextList propsValue={subtext[3]}/>
                <SubTextList propsValue={subtext[4]}/> */}
            </ul>
        </div>
	);
}

export default SubText;