import { useSelector } from "react-redux";

function Gnb(){

	// console.log(data);
	let data=useSelector(state => state.navigation);

    return(
        <div id="gnb">
            <ul>
				{
					data.map((d, i) => <li key={i}><a href="">{d}</a></li>)
				}
                {/* <li><a href="">Home</a></li>
                <li><a href="">About</a></li>
                <li><a href="">Skils</a></li>
                <li><a href="">WebSite</a></li>
                <li><a href="">Contact</a></li> */}
            </ul>
        </div>
    );
}

export default Gnb;