import SkilsContainner from "./SkilsContainer";
import Title from "./Title";
import { useSelector } from "react-redux";
// import data from '../data';

function Skils(){

	// let {title}=data;
	let data=useSelector(state => state.title);
	let {skils}=data;
	
    return(
        <section id="page2"> {/* Skils component */}
            <div className="inner">
            <Title propsValue={skils}/>
            <SkilsContainner />
            </div>
        </section>
    );
}

export default Skils;