// import data from "../data";

function SkilsTitle(props){

	let {delay, h4, p, icon, alt}=props.propsValue;

    return(
        <div className="ux" data-aos="fade-up" data-aos-duration={delay}>
            <h4>{h4}</h4>
            <p>{p}</p>
            <ul>
				{
				icon.map((d, i) => <li key={i}><img src={"./assets/images/"+d.img} alt={d.alt} /></li>)
				}
				{/* <li><img src="./assets/images/HTML_size.png" alt="HTML" /></li>
                <li><img src="./assets/images/CSS_size.png" alt="CSS" /></li>
                <li><img src="./assets/images/JS_size.png" alt="JS" /></li>
                <li><img src="./assets/images/FIGMA_size.png" alt="FIGMA" /></li>
                <li><img src="./assets/images/SASS_size.png" alt="SASS" /></li> */}
            </ul>
        </div>
    );
}

export default SkilsTitle;