import { useNavigate } from "react-router-dom";
import { useEffect } from "react";
import $ from 'jquery'; 
import gsap from "gsap";
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ScrollTrigger from '@terwanerik/scrolltrigger';
import SubHeader from "./component/SubHeader";
import Subopen from "./component/SubOpen";
import SubCase from "./component/SubCase";
import SubSection from "./SubSection";
// import 'swiper/css';

function Subpage(){

	let navigate=useNavigate();
	let link=navigate.state;
	

	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);
		let gnb=document.getElementById("gnb");
		let menuList=gnb.firstElementChild.children;
		let total=document.querySelector(".total");
		let totalList=document.querySelectorAll("#header .total .total_inner > ul > li");
		let tab=document.querySelector(".tab");
		let gnbList=gnb.firstElementChild.children;
		let n=0;
		let totals=gnbList.length;

		menuList[3].classList.add("active");
	
		function portfolioListInit(){
			gsap.to(window, {scrollTo: 0, duration: 0});
			}
		portfolioListInit();
		
		for(let i=0; i<totals; i++){
			menuList[i].addEventListener("click", e => {
				e.preventDefault();
		
				n=i;
		
				if(n === 3) return;
		
				navigate("/", {state: i.toString()});
			});
		}

			let section=document.querySelector("section");
			let project=document.querySelectorAll(".project");

			setTimeout(function(){
				section.classList.add("active");
				project[0].classList.add("active");
			}, 1000);

			let title=document.querySelectorAll(".project .title_area");
			let targety=0;
			
			for (let i=0; i<title.length; i++){
				title[i].addEventListener("click", function(e){
					e.preventDefault();

					for(let j=0; j<project.length; j++){
						if(j === i){
							project[j].classList.add("active");
							targety=project[j].offsetTop;
							gsap.to(window, {scrollTo: targety,duration: 1});
						}
						else {
							project[j].classList.remove("active");
						}
					}
				})
			}


			const trigger=new ScrollTrigger({
				trigger: {
					once: true,
					toggle: {
						class: {
							in: "active",
							out: "inactive"
						}
					},
					offset: {
						viewport: {
							x: 0,
							y: 0.25
						}
					}
				}
			});
		// let projectN=1;
	
		// $(".project .title_area").click(function(e){
		// 	e.preventDefault();
	
		// 	let project=$(this).parents(".project");
	
		// 	if(projectN != project.index()){
				
		// 		projectN=project.index();
		// 		$(".project .fixed_area").removeClass("active");
		// 		project.addClass("active");
	
		// 		let projectY=$(this).offset().top-60;
	
		// 		$("html").animate({scrollTop: projectY}, 800);
		// 	}
		// });

		let imgA=document.querySelectorAll(".list_wrapper .open_source .swiper-slide a")
		for(let i=0; i<imgA.length; i++){
			imgA[0].classList.add("active");
			
			/*
			imgA[i].addEventListener("mouseenter",function(){
				for(let j=0; j<imgA.length; j++){
					if(j === i){
						imgA[j].classList.add("active")
					}
					else{
						imgA[j].classList.remove("active")
					}
				}
			});
			*/
		}
    });
	return (
		<div className="list_wrapper">
			<SubHeader />
			<SubSection />
			<Subopen />
			<SubCase />
		</div> 
	);
}

export default Subpage;