import Title from "./Title";
// import data from '../data';
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

function WebSite(){

	// let {title}=data;
	let data=useSelector(state => state.title);
	let {website}=data;
    return(
        <section id="page3"> {/* Website component */}
            <div className="inner">
            <Title propsValue={website} />
                <TextArea />
            </div>
        </section>
    );
}

function TextArea(){
	let navigate=useNavigate();
    return(
        <div className="text_area" data-aos="fade-up" data-aos-duration="700">
			<button onClick={() => navigate("/subpage")}> WebSite 보러가기 </button> 
			{/* subpage를 PortfolioList로 변경해주면 됨. */}
        </div>
    );
}

export default WebSite;