

function TotalList(props){

	// console.log(props.propsValue);

	let navigation=props.propsValue;
	return(
        <ul>
			{
					navigation.map((d, i) => <li key={i}><a href="">{d}</a></li>)
				}

            {/* 
			<li><a href="">Home</a></li> 
            <li><a href="">About</a></li>
            <li><a href="">Skils</a></li>
            <li><a href="">Website</a></li>
            <li><a href="">Contact</a></li> 
			*/}
        </ul>
	);
}

export default TotalList;