

function ProjectTitle(props){

	let {h3, span, strong}=props.propsValue;

	return (
		<div className="title">
			<a href="">
				<div className="ico"><i className="fas fa-play"></i></div>
				<div className="txt">
					<h3>{h3}</h3>
					<span>{span}</span>
				</div>
				<div className="desc">
				<strong dangerouslySetInnerHTML={{ __html: strong }}></strong>
						{/* <span>React를 사용한 첫 프로젝트며 개인 웹 포트폴리오입니다. 모바일과 PC에서 볼 수 있게 최대한 반응형을 고려하여 작업하였으며 </span>
						router-dom, redux, redux-toolkit에 대한 학습이 되었습니다. 개인 프로젝트의 내용을 정리하고 데이터 베이스를 구축하여 업데이트할 수 있도록 하였습니다. */}
				</div>
			</a>
		</div>
	)
}

export default ProjectTitle;